import React, { Component } from 'react'
import NEWSAPPAPI from 'newsapp'
import { changeUrl, erilizeText, ePreventDefault } from '../utils/util'
export default class CardDialog extends Component {
  constructor(props) {
    super(props)
    this.close = this.close.bind(this) // ,
    this.present = this.present.bind(this) // 
    this.shareCard = this.shareCard.bind(this) // 
    this.giftId = null
  }

  componentDidMount() {
    // 
    this.cardDialog.addEventListener('touchmove', (event) => {
      ePreventDefault(event)
    }, false)
  }

  componentWillUnmount() {
    this.cardDialog.removeEventListener('touchmove', (event) => {
      ePreventDefault(event)
    }, false)
  }

  close() {
    const { changeCardStatus } = this.props.data
    changeCardStatus(false)
  }

  // 
  present() {
    const { cardId, cycleId, cardLen, cardName, prizeName, cardImg, prizeId, changeCardStatus, sendCard, onOpenSD, now, endTime } = this.props.data
    if (now > endTime) {
      onOpenSD('')
      return null
    }
    changeCardStatus(false)
    let shareData = {}
    sendCard(cardId)
      .then((json) => {
        this.giftId = json.data.giftId
        shareData = {
          wbText: '' + changeUrl('http://t.c.m.163.com/uncharted/index.html#/shareCard?cycleId=' + cycleId + '&cardId=' + cardId + '&cardLen=' + cardLen + '&prizeId=' + prizeId + '&giftId=' + encodeURI(this.giftId) + '&', 2),
          wbPhoto: cardImg,
          wxText: '',
          wxTitle: `${cardName}${prizeName}`,
          wxUrl: changeUrl(`http://t.c.m.163.com/uncharted/index.html#/shareCard?cycleId=${cycleId}&cardId=${cardId}&cardLen=${cardLen}&prizeId=${prizeId}&giftId=${encodeURI(this.giftId)}&`, 2),
          wxPhoto: cardImg
        }
        NEWSAPPAPI.share.invoke(shareData, () => {
          onOpenSD('-1')
        })
      })
    return null
  }

  // 
  shareCard() {
    const { data } = this.props
    let wxTitle = `${data.cardName}${data.prizeName},TA`
    const shareData = {
      wbText: '' + changeUrl(`http://t.c.m.163.com/uncharted/index.html#/share?winnStatus=300&cycleId=${data.cycleId}&cardId=${data.cardId}&nowAmount=${data.cardType}&cardLen=${data.cardLen}`, 2),
      wbPhoto: data.cardImg,
      wxText: '',
      wxTitle,
      wxUrl: changeUrl(`http://t.c.m.163.com/uncharted/index.html#/share?winnStatus=300&cycleId=${data.cycleId}&cardId=${data.cardId}&nowAmount=${data.cardType}&cardLen=${data.cardLen}`, 2),
      wxPhoto: data.cardImg
    }
    NEWSAPPAPI.share.invoke(shareData, () => {
    })
  }

  render() {
    const { data } = this.props
    const bgImg = {
      background: `url(${data.cardImg}) no-repeat center`,
      backgroundSize: '100% 100%'
    }
    return (
      <div className="h-card-dialog" ref={(ref) => { this.cardDialog = ref }}>
        <div className="dialog-inner">
          <div className="cover">
            <div className="cover-card-bg" style={bgImg}>
              {
                !!data.cardAmount && <div className="share" onClick={this.shareCard}></div>
              }
            </div>
          </div>
          <footer className="footer">
            <div className="info">{erilizeText(data.cardMark, 28)}</div>
            <div className={parseInt(data.cardAmount, 10) > 1 ? 'btn justify-content1' : 'btn justify-content2'}>
              <div className="close" onClick={this.close}></div>
              {
                parseInt(data.cardAmount, 10) > 1 && <div className="present" onClick={this.present}></div>
              }
            </div>
          </footer>
        </div>
      </div>
    )
  }

}



